import SidebarButton from '~/components/nav/SidebarButton'
import SearchBox from '~/components/nav/SearchBox'
export default {
    components: { SidebarButton, SearchBox },
    computed: {
        activeClass() {
            return this.$route.path.startsWith('/blog')
                ? 'nuxt-link-exact-active'
                : ''
        }
    }
}
